import React from 'react';
import Spinner from 'react-bootstrap/Spinner';
import { AiOutlineRight } from 'react-icons/ai';
import { toast } from 'react-toastify';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import { emailValidator } from "libs/validators";
import AdminServices from 'services/api/AdminServices';
import AuthHeader from 'views/mobileView/components/molecules/Headers/AuthHeader';
import { browserName, deviceType, mobileModel, osName, osVersion } from 'react-device-detect';
import db from 'libs/db';
export default function OtherDetail(props) {

    const [email, setEmail] = React.useState('');
    const [mobile, setMobile] = React.useState('');
    const [verify, setVerify] = React.useState('');
    const [skipPressed, setSkipPressed] = React.useState(false);
    const { state } = props.location;

    React.useEffect(() => {
        if (state === null || state === undefined) {
            props.history.push('/signup')
        }
        else {
            setVerify(state.verify === 'EMAIL' ? 'MOBILE' : 'EMAIL')
        }
    }, [])

    const validateInput = () => {
        if (verify == 'EMAIL') {
            return emailValidator(email).isValid
        }
        else {
            return mobile.length === 10
        }
    }

    const onNextBtnPressed = async () => {
        if (verify === 'EMAIL') {
            props.history.push({
                pathname: '/verify-otp2',
                state: { ...state, email, verify }
            })
        }
        else {
            props.history.push({
                pathname: '/verify-otp2',
                state: { ...state, mobile, verify }
            })
        }
    };

    const createUser = async () => {
        setSkipPressed(true);
        const data = {
            email: state.email,
            clientName: state.name,
            source: deviceType + ":" + browserName + ":" + (mobileModel === "none" ? osName : mobileModel) + ":" + osVersion
        }
        try {
            const res = await AdminServices.createUser(data).then((res) => res)
            if (res.type) {
                if (res.data.Result === 'success') {
                    const userData = {
                        name: state.name,
                        email: state.email,
                        id: res.data.ClientId,
                        isLogged: true,
                    };
                    await db.set(db.model.user, JSON.stringify({ userData }));
                    props.history.push({
                        pathname: '/dashboard',
                        state: { ...state }
                    })
                    setSkipPressed(false);
                } else {
                    toast.error("Try again ...");
                    setSkipPressed(false);
                }
            } else {
                toast.error("Try again ...");
                setSkipPressed(false);
            }
        } catch (error) {
            toast.error("Try again ...");
            setSkipPressed(false);
        }
    }

    return (
        <div className="mainContainer">
            {
                verify === 'EMAIL' ?
                    <AuthHeader navigation={props.history} left='login' />
                    :
                    <AuthHeader navigation={props.history} left='login' right="skip" onSkip={createUser} />
            }
            <div className="dataContainer">
                <div className="login-header">
                    <div className="bigHeader">{verify === 'EMAIL' ? "Let’s verify your email address" : "Let’s verify your mobile number"}</div>
                    {verify === 'EMAIL' ?
                        <div className='bodyTextDark' style={{ marginTop: "6px" }}>
                            Please enter your email address<br /> No spam ever!
                        </div>
                        :
                        <div className='bodyTextDark' style={{ marginTop: "6px" }}>
                            Please enter your 10 digit Indian mobile number <br /> No unsolicited calls ever!
                        </div>
                    }
                </div>
                {verify === 'EMAIL' ?
                    <FloatingTextInput
                        type="email"
                        label="Email address"
                        autoFocus
                        onChange={(p) => setEmail(p.target.value)}
                        value={email}
                    /> :
                    <FloatingTextInput
                        type="tel"
                        label="Mobile number"
                        autoFocus
                        id="Mobile-number"
                        placeholder=" "
                        maxLength={10}
                        value={mobile}
                        onChange={(num) => {
                            const n = num.target.value.replace(/\D/g, '');
                            setMobile(n);
                        }}
                    />
                }
            </div>
            <div className="login-footer">
                {
                    skipPressed ?
                        <button className="solidButton" style={{ opacity: 0.4 }}>
                            <Spinner
                                style={{ color: "#fff" }}
                                as="span"
                                animation="border"
                                size="sm"
                                role="status"
                                aria-hidden="true"
                            />
                        </button>
                        :
                        <button className="solidButton" style={{ opacity: validateInput() ? 1 : 0.4 }}
                            onClick={() => { validateInput() && onNextBtnPressed() }}>
                            Get OTP <AiOutlineRight size={12} />
                        </button>
                }
            </div>
        </div>
    )
}
